import { Component, OnInit } from "@angular/core";
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl
} from "@angular/forms";
import { UserService } from "../_services/user.service";

@Component({
  selector: "app-events",
  templateUrl: "./events.component.html",
  styleUrls: ["./events.component.css"]
})
export class EventsComponent implements OnInit {
  events: any;
  employees: any;
  addForm: FormGroup;
  employee: any;
  temperID:any;
  displayMsg = false;
  constructor(
    private userservice: UserService,
    private formBuilder: FormBuilder
  ) {}

  ngOnInit(): void {
    this.addForm = this.formBuilder.group({
      id: ["", Validators.required]
    });
    this.getevents();
  }

  removeTask(id){
    this.temperID=id;
    console.log(this.temperID);
  }

  get f() {
    return this.addForm.controls;
  }

  onSubmit() {
    this.userservice.getEmployee(this.f.id.value).subscribe(
      data => {
        this.employee = data["data"];
        console.log(this.employee.id,this.employee.name);
          this.userservice.getEventEmployee(this.temperID,this.employee.id).subscribe(
            data => {
              if(data['data']==null){
                console.log("Can add");
                this.addEmployee(this.employee.id,this.employee.name,this.employee.department,this.employee.position,this.temperID);
                this.getEmployees();
              }
              console.log(data['data']);
            },err =>{
              console.log(err);
            }
          )
        //this.findEmployee(this.temperID,this.employee.id);
        // this.addEmployee(this.employee.id,this.employee.name,this.employee.department,this.employee.position,this.temperID);
        // this.getEmployees();
      },
      err => {
        console.log(err);
      }
    );
    this.addForm.reset();
    this.displayMsg=false;
  }

  addEmployee(eid,name,department,position,id) {
    this.userservice.addEventEmployee(eid,name,department,position,id).subscribe(
      data => {
        if(data['status']==200){
          this.displayMsg=true;
        }
        console.log(data);
      },
      err => {
        console.log(err);
      }
    );
  }

  hideMsg(id){
    this.displayMsg=false;
    this.removeTask(id);
    this.getEmployees();
  }

  getevents() {
    this.userservice.getAllEvents().subscribe(
      data => {
        this.events = data["data"];
        console.log(this.events);
      },
      error => {
        console.log(error);
      }
    );
  }


  getEmployees() {
    this.userservice.getAllEventEmployees(this.temperID).subscribe(
      data => {
        this.employees = data["data"];
        console.log(this.employees);
      },
      err => {
        console.log(err);
      }
    );
  }
}
